<template>
  <div id="articles">
    <div class="web-list" v-if="!detailStart">
      <ul>
        <li v-for="(val, i) in page.show" v-show="i<num">
          <div class="content">
            <div class="imgs" >
              <img :src="val.img_addr|srcFilters" alt="img" @click="showArticle(val)">
            </div>
            <div class="text">
              <div class="text-top">
                <i>{{val.show_type|toUpper}}</i>
                <span v-text="val.header" @click="showArticle(val)"></span>
              </div>
              <div class="text-content" @click="showArticle(val)"><span v-text="val.text"></span></div>
            </div>
          </div>
          <div class="infos">
            <div class="infos-left" v-if="showNav">
              <div><Icon type="md-time" /><span v-text="val.times"></span></div>
              <div><Icon type="md-chatboxes" /><span>{{val.comment.length}}评论</span></div>
              <div><Icon type="md-eye" /><span>{{val.read}}次阅读</span></div>
              <div><Icon type="md-thumbs-up" /><span>{{val.like}}点赞数</span></div>
            </div>
            <div class="infos-left" v-if="!showNav">
              <!--适配手机宽度 避免内容多导致换行-->
              <div><Icon type="md-time" /><span v-text="val.times"></span></div>
              <div><Icon type="md-chatboxes" /><span>{{val.comment.length}}查看</span></div>
              <div><Icon type="md-eye" /><span>{{val.read}}阅读</span></div>
              <div><Icon type="md-thumbs-up" /><span>{{val.like}}点赞</span></div>
            </div>
            <div class="infos-right" @click="showArticle(val)">
              全文<Icon type="ios-arrow-dropright-circle" />
            </div>
          </div>
        </li>
      </ul>
      <h3 v-if="page.show.length === 0"><Icon type="logo-android" />博主还没有发布该分类文章哦!</h3>
      <div class="page">
        <Page :total="info.length" @on-change="changePage" show-total :page-size="page.pageNum" />
      </div>
    </div>
    <showarticle @baseChangeStatus="changeStatus" ref="product" :showdata="detail" v-if="detailStart"></showarticle>
  </div>
</template>

<script>
  import showarticle from './showarticle';
    export default {
      name: "article-list",
      data(){
        return{
          url: "http://120.79.223.5:6060",
          count : 60,
          cmdb:[],
          info:[],
          page:{
            pageNum:6,
            show:[],
          },
          detail:{},
          moreText:"阅读更多",
          moreStatus:true,
          detailStart: false,
          num:6,
          showNav:false,
        }
      },
      components:{
        showarticle
      },
      props:['param'],
      mounted(){
        this.getWidth();
      },
      watch :{
        param:{
          handler(){
            this.getCmdb(this.param.flag);
          },
          // 页面加载之初先执行一次handle
          immediate: true,
          // 深度检查属性,即使对象内部的属性值改变, 也能检测到(比较消耗性能)
          deep: true
        }
      },
      filters:{
        srcFilters(value){
          return value.replace("/www/wwwroot/dqm.zixxxx.com/", "/");
        },
        toUpper(value){
          let n = value.length;
          let one = value[0].toUpperCase();
          return one + value.slice(1, n)
        },
      },
      methods:{
        getCmdb(flag){
          let obj = {
            "collection" : this.filter(flag),//过滤值
            "num": this.count
          };
          this.cmdb = [];
          this.info=[];
          this.page.show=[];
          this.$http.post(this.url + "/base/api/getcmdb", this.$qs.stringify(obj)).then(recv=>{
            this.cmdb = recv.data;
            this.groups(flag); //css html 分组
            if(this.info.length>=this.page.pageNum){ // 分页
              this.page.show = this.info.slice(0, this.page.pageNum);
            }else{
              this.page.show = this.info;
            }
          }, err=>{
            this.cmdb =[];
            this.page.show = [];
            this.info = [];
            this.$Notice.error({
              title: '通知消息',
              desc: "获取最新动态数据错误:"+ err.message
            });
          })
        },
        groups(flag){
          let tmp = flag.toUpperCase();
          if(tmp !== "HTML"&&tmp!=="CSS"){
            this.info = this.cmdb;
            return
          }
          if(this.cmdb.length === 0){
            this.info = [];
            return
          }
          let that = this;

          this.cmdb.forEach(function (ele) {
            if(tmp === ele.show_type.toUpperCase()){
              that.info.push(ele);
            }
          })
        },
        filter(value){
          let tmp = value.toUpperCase();
          if(tmp === "HTML"||tmp==="CSS"){
            return 'hc'
          }else if(tmp === 'JS'){
            return 'js'
          }else if(tmp === 'PYTHON'){
            return 'python'
          }else if(tmp === 'GOLANG'){
            return 'golang'
          }
        },
        getWidth(){
          let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
          if(w>750){
            this.showNav=true;
          }
        },
        changePage(index){
          let _start = (index -1) * this.page.pageNum;
          let _end = index * this.page.pageNum;
          this.page.show = this.info.slice(_start, _end);
        },
        changeStatus(){
          this.detailStart = !this.detailStart
        },
        showArticle(data){
          this.detail = data;
          this.detail["login"] = this.param.login;
          this.detail["change"] = "baseChangeStatus";
          this.detailStart = !this.detailStart;
          let obj = {
            "idNum" :data.id,
            "showType":data.show_type
          };
          this.$http.post(this.url + "/web/api/addRead", this.$qs.stringify(obj)).then(res=>{}, err=>{
            console.log(err)
          })
        },
      }
    }
</script>

<style scoped lang="less">
  /*start less类*/
  .xflex(@num){
    flex: @num;
    -webkit-flex: @num;
    -ms-flex: @num;
    -moz-box-flex: @num;
  }
  .xdisplayflex(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex
  }
  .xboxshow(){
    -moz-box-shadow:1px 1px 5px #515151;
    -webkit-box-shadow:1px 1px 5px #515151;
    box-shadow:1px 1px 5px #515151;
  }
  .bgimg(@path){
    background-image: url(@path);
    -moz-background-size:cover; /* 老版本的 Firefox */
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center center;
  }
  .h2(){
    h2{
      border-bottom: 1px solid rgba(83, 83, 83, 0.15);
    }
    h2::after{
      content: "";
      display: block;
      width: 18%;
      border: 1px solid rgb(0, 162, 255);
      vertical-align: middle;
    }
  }
  @color: rgb(179, 179, 179);
  @textcolor: #0097ee;
  /*end less类*/
  @media (max-width: 750px){
    #articles{
      .web-list{
        h3{
          font-size: 18p/32rem;
          color: @color;
          margin: 300/32rem 0;
        }
        .page{
          margin: 32/32rem 0;
        }
        ul{
          li{
            margin-bottom: 26/32rem;
            .content{
              .xdisplayflex();
              flex-wrap: wrap;
              -webkit-flex-wrap: wrap;
              -ms-flex-wrap: wrap;
              margin-bottom: 30/32rem;
              .imgs{
                .xflex(3);
                margin-right: 20/32rem;
                img{
                  width: 240/32rem;
                  height: 145/32rem;
                }
                img:hover{
                  cursor: pointer;
                }
              }
              .text{
                .xflex(7);
                text-align: left;
                .text-top{
                  margin-bottom: 10/32rem;
                  i{
                    color: #fff;
                    font-size: 15/32rem;
                    background: @textcolor;
                    padding: 3/32rem 10/32rem;
                    -webkit-border-top-right-radius: 50%;
                    -webkit-border-bottom-right-radius: 50%;
                    border-top-right-radius:50%;
                    border-bottom-right-radius:50%;
                  }
                  span{
                    font-size: 16/32rem;
                  }
                  span:hover{
                    color: @textcolor;
                    cursor: pointer;
                  }
                }
                .text-content{
                  font-size:12/32rem;
                  color: @color;
                  span{
                    font-size:12/32rem;
                    color: @color;
                  }
                }
              }
            }
            .infos{
              .xdisplayflex();
              color: @color;
              font-size: 13/32rem;
              .infos-left{
                .xflex(8.5);
                .xdisplayflex();
                align-items: center;
                div{
                  .xdisplayflex();
                  align-items: center;
                  margin-right: 10/32rem;
                  i{
                    margin: 0 3/32rem;
                  }
                  i:first-child{
                    margin-left: 0;
                  }
                }

              }
              .infos-right{
                .xflex(1.5);
                .xdisplayflex();
                align-items: center;
                i{
                  margin-left: 5/32rem;
                }
              }
              .infos-right:hover{
                color: @textcolor;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
  @media (min-width: 750px){
    #articles{
      .web-list{
        h3{
          font-size: 18px;
          color: @color;
          margin: 300px 0;
        }
        .page{
          margin: 32px 0;
        }
        ul{
          li{
            margin-bottom: 26px;
            .content{
              .xdisplayflex();
              flex-wrap: wrap;
              -webkit-flex-wrap: wrap;
              -ms-flex-wrap: wrap;
              margin-bottom: 30px;
              .imgs{
                .xflex(3);
                margin-right: 20px;
                img{
                  width: 240px;
                  height: 145px;
                }
                img:hover{
                  cursor: pointer;
                }
              }
              .text{
                .xflex(7);
                text-align: left;
                .text-top{
                  margin-bottom: 10px;
                  i{
                    color: #fff;
                    font-size: 15px;
                    background: @textcolor;
                    padding: 3px 10px;
                    -webkit-border-top-right-radius: 50%;
                    -webkit-border-bottom-right-radius: 50%;
                    border-top-right-radius:50%;
                    border-bottom-right-radius:50%;
                  }
                  span{
                    font-size: 20px;
                  }
                  span:hover{
                    color: @textcolor;
                    cursor: pointer;
                  }
                }
                .text-content:hover{
                  cursor: pointer;
                }
                p{
                  font-size: 15px;
                }
              }
            }
            .infos{
              .xdisplayflex();
              color: @color;
              font-size: 13px;
              .infos-left{
                .xflex(8.5);
                .xdisplayflex();
                align-items: center;
                div{
                  .xdisplayflex();
                  align-items: center;
                  margin-right: 10px;
                  i{
                    margin: 0 3px;
                  }
                  i:first-child{
                    margin-left: 0;
                  }
                }

              }
              .infos-right{
                .xflex(1.5);
                .xdisplayflex();
                align-items: center;
                i{
                  margin-left: 5px;
                }
              }
              .infos-right:hover{
                color: @textcolor;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }


</style>



